.ma__icon-links {

  &__items {
    @include ma-reset-list;
  }

  &__item {
    position: relative;
  }

  &__icon {
    font-size: 0;
    position: absolute;
      top: 5px;
      left: 0;

    & > svg {
      height: 18px;
      width: 20px;
    }
  }

  &__link {
    font-size: 1.25rem;
    display: block;
    padding-left: 30px;
  }
}

//theme
.ma__icon-links {

  &__icon {

    & > svg,
    & > svg path {
      fill: $c-primary-alt;
    }
  }
}
